@import url('https://fonts.googleapis.com/css2?family=Kanit:wght@300&display=swap');
footer{
    height:15vh;
    margin: 0;
    position: absolute;
    bottom: 0;
    left:0;
    right: 0;
    background-color: darkslateblue;
    width: 85vw;
    display: none;
    color: white;
    font-family: 'Kanit', sans-serif;
    margin-left:15vw;
}
#copyright{
    font-size:x-small;
    margin-bottom: -5px;
}
#orgName{
    text-align: center;
    margin: auto;
    font-size:x-large;
}
#contact{
    margin:auto;
    display: flex;
    flex-direction: column;
    text-align: center;
}

#contact p{
    margin-top:2px;
}
#contact img{
    height:25px;
    width: 25px;
    margin: 0px 5px;
    margin-top: 15px;
    margin-bottom: -2px;

}
#socialLinks{
    /* margin: auto; */
    list-style: none;
}
#socialLinks a{
    text-decoration: none;
    color: white;
}

@media screen and (max-width:750px){
    footer{
        display: none;
    }
}